.footer {
	display: flex;
	justify-content: space-between;
	width: 100%;
	border-top: 1px solid #edeff1;
	// margin-bottom: 25px
	padding: 16px 17px 9px 20px;
	color: #2e3236;
	font-size: 11px;
	font-weight: 400;
	@include r(620) {
		display: block; }
	a {
		color: #2e3236; }
	&__navig {
		display: flex;
		@include r(620) {
			justify-content: center;
			margin-bottom: 10px; }
		.block {
			margin-right: 60px;
			@include r(710) {
				margin-right: 30px; }
			@include r(423) {
				margin-right: 15px; }
			li {
				padding-bottom: 5px; } } }
	.copy {
		text-align: right;
		@include r(620) {
			text-align: center; } } }


